import { Component } from '@angular/core';
import { NavController, PopoverController, ToastController, ToastOptions, AlertController } from 'ionic-angular';
import { InfoPage } from '../info/info';
import { turn, client } from '../../tiposdatos';

import { Storage } from '@ionic/storage';
import { FirebaseServiceProvider } from '../../providers/firebase-service/firebase-service';
import { MainMenuComponent } from '../../components/main-menu/main-menu';
import { ConfigPage } from '../config/config';

import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { QscannerComponent } from '../../components/qscanner/qscanner';
import { DetailPage } from '../detail/detail';
import { Subscription } from 'rxjs';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {


  TurnEventSubsc : Subscription;
  turnList : turn[] = [];
  turnOrder : number = 0;
  tostOptions : ToastOptions;

  constructor(public navCtrl:NavController,
    private fds:FirebaseServiceProvider, 
    private af:Storage,
    private popCtrl: PopoverController,
    private QrScanner : BarcodeScanner,
    private toastMsg :ToastController,
    private alertCtrl : AlertController) {
  }


  ngOnInit() {
    this.af.get('clientId').then(data => {
      //recupero de firestore la información del registro
      let myId = data as string;
      if(!data) {myId = this.onCreateClient();}
      this.fds.getClientInfo(myId).then(doc => {
        if (doc.data() as client) {
          //si la obtengo, la guardo en una variable del servicio firestoredata.service
          this.fds.itemClient = doc.data() as client;
          //No se ha llenado el formulario
          if (this.fds.itemClient.title=='' || this.fds.itemClient.id=='') {
            this.presentToast('Es buen momento para completar su perfil',5000);
          };
          this.TurnEventSubsc = this.fds.addTurnObservable().subscribe(aList => {
            this.turnList = [];
            this.turnList = aList as turn[];
            for(let i=0;i<this.turnList.length;i++) {this.onchangeEventTurn(i)};
          })
          
        } else {
          this.onAlert('Error datos eliminados','Debe ingresar de nuevo la información');
          this.onCreateClient();
        }
      }).catch(error => console.log(error));
    }, (error) =>{
      this.onAlert('Error datos eliminados','Debe ingresar de nuevo la información');
      this.onCreateClient();
    });
  }

  async showMainMenu(thisEvent:Event) {
    let pvr = this.popCtrl.create(MainMenuComponent);
    pvr.present({ev:thisEvent});
    pvr.onDidDismiss(dt => {
      if(dt) {
        this.navigateTo(dt)
      }
    })
  }

  navigateTo(rt: string) {
    switch(rt) {
      case 't':
      this.navCtrl.push(HomePage);
      break;
      case 'i':
      this.fds.infoIsPage = true;
      this.navCtrl.push(InfoPage);
      break;
      case 's':
      this.navCtrl.push(ConfigPage);
      break;
    }
  }

/********************************** SCANNER SCANNER SCANER ********************************** */

  showQScanner(ev:any) {
    this.QrScanner.scan().then(barcodeData => {
      let msg = this.fds.Securityinfo(this.fds.itemClient.security_level);
      this.fds.addTurnEvent(barcodeData.text,msg);
    }).catch(err => {
      this.presentToast(err, 2000);
      this.showQScannerAlternative();
    });
  }

  /*async showQScanner(ev:any) {
    const modal = await this.modalCtrl.create({component: QscannerComponent});
    await modal.present();
  }*/

  async showQScannerAlternative() {
    let pvr = this.popCtrl.create(QscannerComponent);
    pvr.present();
    pvr.onDidDismiss(data =>{
      let msg = this.fds.Securityinfo(this.fds.itemClient.security_level);
      this.fds.addTurnEvent(data,msg);
      navigator.vibrate(100);
    })
  }
  
/*************************************************************************** */

  onCreateClient() {
    let client: client = {};
    client.myId = this.fds.NewId();
    client.addr = '';
    client.addr_geo = '';
    client.security_level = 0;
    client.email ='';
    client.id = '';
    client.logo = '';
    client.phone = '';
    client.title = '';
    client.type = 'C';
    this.fds.updateClient(client);
    this.fds.itemClient = client;
    this.af.set('clientId', client.myId);
    return client.myId;
  }

  /*
  async presentToast() {
    const toast = await this.toastMsg.create({
      message: 'Your settings have been saved.',
      duration: 2000
    });
    toast.present();
  }
  */

  presentToast(msg:string, timeOn:number) {
    this.tostOptions = {
      message: msg,
      showCloseButton: true,
      duration: timeOn,
      position: 'bottom',
      closeButtonText: 'Editar'
    };

    this.toastMsg.create(this.tostOptions).present();
    
  };

  //******************************* TURN TURN EVENTS EVENTS ***************************/

  onchangeEventTurn(index:number) {
    let t = this.turnList[index] as turn;
    t.open = t.status=='A' || t.status=='C' || t.status=='P';
    if(t.event==='_') {
      switch(t.status){
        case 'A':
          t.status_string = 'Atendiendo...';
        break;
        case 'C':
          t.status_string = 'Llamada...';
        break;
        case 'P':
          t.status_string = 'Pausado...';
        break;
        case 'S':
          t.status_string = 'Asignado...';
        break;
        case 'K':
        t.status_string = 'Aplazado...';
        break;
      }
    }
  }

  onDeleteEvent(turnId:number){
    this.fds.someEvent(this.turnList[turnId],'D','');
    //Elimino el registro para asegurar que borra aun si el servidor esta inactivo.
    this.turnList.splice(turnId, 1);
  }

  onPauseEvent(thisTurn: turn) {
    this.fds.someEvent(thisTurn,'P','');
  }

  onContinueEvent(thisTurn: turn) {
    this.fds.someEvent(thisTurn,'S','');
  }

  onTapCard(thisTurn: turn){
    this.fds.infoIsPage = false; //Para esconder el navegador de info
    this.fds.actualItemTurn = thisTurn;
    this.navCtrl.push(DetailPage);
  }
  
  onStateOfTurn(turnItem: turn) {
    if (turnItem.status == 'S' || turnItem.status == 'K') {
      return turnItem.assignedTurn + ' | Pos: '+ turnItem.pos + ' | ' + turnItem.stat + ' min';
    } else if (turnItem.status == 'C' || turnItem.status == 'A') {
      return turnItem.window_title;
    }
  }

  onOrder() {
    if(this.turnOrder==1) {this.turnOrder=0} else {this.turnOrder=1}
    switch(this.turnOrder){
      case 0:
        return 'Posicion';
      case 1:
        return 'Tiempo';
    }
  }

  onAlert(title:string,msg:string) {
    this.alertCtrl.create({
      title,
      subTitle: msg,
      buttons: ['Dismiss']
    }).present();
  }

}



