import { Component,OnInit, ViewChild } from '@angular/core';
import { ZXingScannerComponent } from '@zxing/ngx-scanner';
//import { FirebaseServiceProvider } from '../../providers/firebase-service/firebase-service';
import { Subscription } from 'rxjs';
import { ViewController } from 'ionic-angular';

/**
 * Generated class for the QscannerComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'qscanner',
  templateUrl: 'qscanner.html'
})
export class QscannerComponent implements OnInit{
  @ViewChild('scanner')
  scanner: ZXingScannerComponent;

  hasCameras = false;
  hasPermission: boolean;
  qrResultString: string;

  foundCmrSubs: Subscription;
  noFoundCmrSubs : Subscription;
  permSubs : Subscription;

  availableDevices: MediaDeviceInfo[];
  selectedDevice: MediaDeviceInfo;

  code : string = 'NJHCVGMe4XHocFlZguNI'; //OJO SOLO POR AHORA

  constructor(private popCtrl:ViewController) { }

  ngOnInit(): void {

    this.foundCmrSubs = this.scanner.camerasFound.subscribe((devices: MediaDeviceInfo[]) => {
      this.hasCameras = true;

      console.log('Devices: ', devices);
      this.availableDevices = devices;

      //selects the devices's back camera by default
      for (const device of devices) {
        if (/back|rear|environment/gi.test(device.label)) {
          this.scanner.changeDevice(device);
          this.selectedDevice = device;
          break;
        }
      }
    });

    this.noFoundCmrSubs = this.scanner.camerasNotFound.subscribe((devices: MediaDeviceInfo[]) => {
        console.error('An error has occurred when trying to enumerate your video-stream-enabled devices.');
    });

    this.permSubs = this.scanner.permissionResponse.subscribe((answer: boolean) => {
      this.hasPermission = answer;
    });

  }

  handleQrCodeResult(resultString: string) {
      //console.log('Result: ', resultString);
      //this.fds.addTurnEvent(resultString);
      //navigator.vibrate(100);
      this.popCtrl.dismiss(resultString);
  }

  onDeviceSelectChange(selectedValue: string) {
      //console.log('Selection changed: ', selectedValue);
      this.selectedDevice = this.scanner.getDeviceById(selectedValue);
  }

  onManualIng() {
    //this.fds.addTurnEvent(this.code);
    //navigator.vibrate(100);
    this.code = prompt('Escriba el código de acceso','');
    if(this.code) {this.popCtrl.dismiss(this.code);};
          //this.router.navigate(['/turns']); //.catch(rason => console.log(rason));
  }

  ngOnDestroy() {
    this.foundCmrSubs.unsubscribe();
    this.noFoundCmrSubs.unsubscribe();
    this.permSubs.unsubscribe();
  }
}



