import { Component, OnInit } from '@angular/core';
import { anItem } from '../../tiposdatos';
import { FirebaseServiceProvider } from '../../providers/firebase-service/firebase-service';
import { Subscription } from 'rxjs';
import { AlertController } from 'ionic-angular';

/**
 * Generated class for the DetaItemComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'deta-item',
  templateUrl: 'deta-item.html'
})

export class DetaItemComponent implements OnInit{

  items: anItem[] = [];
  itemSubs : Subscription;
  windClient : string = '';
  edited: boolean = false;

  itemstaxTtl: string = '';
  itemsTtl: string = '';

  alternateclass: string= '';

  constructor(private fds:FirebaseServiceProvider, private moreDetaSelect:AlertController) {}

  ngOnInit(){
    debugger;
    this.itemSubs = this.fds.addTurnItemObservable(this.fds.actualItemTurn.itemId).subscribe(chatList => {
      debugger;
      this.windClient = this.fds.actualItemTurn.window_title;
      if(chatList.itemChatStream) {
        this.items = JSON.parse(chatList.itemChatStream);
        this.calculate();
        this.edited=false;
        this.alternateclass = 'values';
      }
    });
  }

  ngOnDestroy() {
    debugger;
    this.itemSubs.unsubscribe();
  }

  onMore(item:anItem) {
    item.amount++;
    item.total=item.amount*item.price;
    this.edited=true;
    this.alternateclass = 'values-before';
    this.calculate();
  }

  onLess(item:anItem) {
    if(item.amount>0) {
      item.amount--;
      item.total=item.amount*item.price;
      this.edited=true;
      this.alternateclass = 'values-before';
      this.calculate();
    }
  }

  calculate() {
    var tx:number = 0;
    var tt:number = 0;
    this.items.forEach(i => {
      tx += (i.amount*i.tax);
      tt += (i.amount*i.price);
    });
    this.itemstaxTtl = 'Imp.' + this.toCurrencyStr(tx);
    this.itemsTtl = 'Total' + this.toCurrencyStr(tt);
  }

  sendItemList() {
    if(this.edited) {
      this.fds.itemEvent(this.items);  
    }
  };

  reloadItems() {
    
  }

  showDetaItem(item:anItem) {
    let act = this.moreDetaSelect.create();
    act.setTitle(item.title);
    act.setMessage(item.description);
    act.addButton('Salir');
    act.present();
  }

  toCurrencyStr(n:number) {
    return '$ '+(n).toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,');;
  }

}



