import { Component, OnInit, ViewChild } from '@angular/core';
import { FirebaseServiceProvider } from '../../providers/firebase-service/firebase-service';
import { chat } from '../../tiposdatos';
import { Content, AlertController } from 'ionic-angular';
import { Subscription } from 'rxjs';

/**
 * Generated class for the DetaChatComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'deta-chat',
  templateUrl: 'deta-chat.html'
})
export class DetaChatComponent  implements OnInit{
  @ViewChild('mycontent') content: Content;

  chats: chat[] = [];
  windClient: string;
  message: string = '';
  itemChatSubs : Subscription;

  constructor(private fds:FirebaseServiceProvider, private SecuritySelect:AlertController) {

  }

  ngOnInit(){
    this.windClient = this.fds.actualItemTurn.window_title;
    this.itemChatSubs = this.fds.addTurnChatObservable(this.fds.actualItemTurn.chatId)
    .subscribe(chatInfo => {
      if(chatInfo.itemChatStream) {
        this.chats = JSON.parse(chatInfo.itemChatStream);
        setTimeout(()=> {
          if(this.content._scroll){this.content.scrollToBottom(0)}
        }, 200); 
      }
      
    });
  }

  sendMessage(){
    if(this.message) {
      this.fds.chatEvent(this.chats, this.message);
      this.message='';
    }
  }

  showSecuritySel() {
    let act = this.SecuritySelect.create();
    act.setTitle('Seguridad');
    act.setMessage('Información disponible para el proveedor');
    act.addInput({type:'radio',label:'No información',value:'0',checked:this.fds.actualItemTurn.security==0});
    act.addInput({type:'radio',label:'Nombre',value:'1',checked:this.fds.actualItemTurn.security==1});
    act.addInput({type:'radio',label:'Nombre + identificación',value:'2',checked:this.fds.actualItemTurn.security==2});
    act.addInput({type:'radio',label:'Información contacto',value:'3',checked:this.fds.actualItemTurn.security==3});
    act.addButton('Salir');
    act.addButton({
      text:'Salvar',
      handler: (data) => {
        let m: string = '';
        this.fds.actualItemTurn.security = Number(data);
        if(this.fds.actualItemTurn.security>0){m=this.fds.itemClient.title;} else {m=this.fds.actualItemTurn.turnCode}
        this.fds.actualItemTurn.assignedTurn=m;
        this.fds.someEvent(this.fds.actualItemTurn,data,m);
        let levelInfo = this.fds.Securityinfo(this.fds.actualItemTurn.security);
        //levelInfo = this.fds.Securityinfo();
        this.fds.chatEvent(this.chats, levelInfo);
      }
    });
    act.present();
  }

  ngOnDestroy() {
    this.itemChatSubs.unsubscribe();
  }

}



