import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import { client, turn, tmrEvents, itemChatList, chat, anItem } from '../../tiposdatos';

/*
  Generated class for the FirebaseServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class FirebaseServiceProvider {

  itemClient : client = {type:"N"};
  actualItemTurn: turn = {};
  infoIsPage: boolean=false;

  constructor(private afs: AngularFirestore) {}

  //Id inicial, para subscribirse
  getClientInfo(ClientId:string) {
    return this.afs.collection('client').doc(ClientId).ref.get();
  }

  updateClient(Client:client) {
    this.itemClient = Client;
    this.afs.collection('client').doc(Client.myId).ref.set(Client);
  }

  deleteClient(Client:client) {

  }

  NewId(){
    return this.afs.createId();
  }

  //............................. TURNOS ..............................
  //............................. TURNOS ..............................
  //............................. TURNOS ..............................

  //turnEventCollection : AngularFirestoreCollection<turn>;

  getTurnInfo(turnInd:string){
    return this.afs.collection('events').doc(turnInd).ref.get();
  }

  //Observamos la lista de eventos hacia el servidor
  addTurnObservable(){
    return this.afs.collection<turn>('turn', ref => {
      return ref
        .where('active','==',true)
        .where('key_client','==',this.itemClient.myId)
    }).valueChanges();
    //return this.turnEventCollection.valueChanges();
  }

  getOneTurnObservable(turnInd:string){
    //return this.afs.collection('turn').doc(turnInd).ref.get();
    return this.afs.collection<turn>('turn', ref => {
      return ref
        .where('myId','==',turnInd)
    }).valueChanges();
  }

  //................................ EVENTOS EVENTOS EVENTOS EVENTOS .....................................
  //................................ EVENTOS EVENTOS EVENTOS EVENTOS .....................................
  //................................ EVENTOS EVENTOS EVENTOS EVENTOS .....................................

  addEvent(myEvent:tmrEvents){
    myEvent.myId = this.afs.createId();
    this.afs.collection('events').doc(myEvent.myId).ref.set(myEvent);
  }

  //Solicita agregarse a la lista de turnos. El evento inicia en 
  addTurnEvent(serverId:string,clientInfo:string) { 
    let myEvent : tmrEvents = {};
    myEvent.event = 'N';
    myEvent.key_server = serverId;
    myEvent.request_id = this.itemClient.id;
    myEvent.request_downloadURL = this.itemClient.downloadURL || '_';
    myEvent.request_myId = this.itemClient.myId;
    myEvent.request_title = this.itemClient.title;
    myEvent.request_security = this.itemClient.security_level;
    //Información de saludo de acuerdo a nivel de seguridad
    myEvent.key_turn=clientInfo;
    this.addEvent(myEvent);
  };

  someEvent(thisTurn:turn,thisEvent:string, msg:string){
    let myEvent : tmrEvents = {};
    myEvent.event = thisEvent; //S, P, cambio de nivel de seguridad 1 2 3, I mensaje
    myEvent.key_server = thisTurn.key_server;
    myEvent.key_turn = thisTurn.myId;
    myEvent.request_title = msg;//Mensaje
    myEvent.request_id = 'C';//En caso de mensaje, si es C o es S
    this.addEvent(myEvent);
  }

  //Productos seleccionados. Tal vez solo se necesiten estos eventos y el componente agrega o edita
  addEdtiItemEvent(){}

  deleteItemEvent(){}

  //................................... TURN CHAT ITEM ..............................
  //................................... TURN CHAT ITEM ..............................
  //................................... TURN CHAT ITEM ..............................  

  addTurnChatObservable(chatId:string){
    return this.afs.doc<itemChatList>('chats/'+chatId).valueChanges();
  }

  addTurnItemObservable(itemId:string){
    return this.afs.doc<itemChatList>('items/'+itemId).valueChanges();
  }

  updateItemChat(myId:string, type:string, list:string) { //type: chats, items
    debugger;
    let itemChat: itemChatList = {};
    itemChat.itemChatStream = list;
    this.afs.collection(type).doc(myId).ref.set(itemChat);
  }

  chatEvent(chats:chat[], msg:string){
    //agrego al chat
    let c: chat = {};
    c.date = new(Date); //OJO
    c.msg = msg;
    c.origin = 'C';
    c.pos = chats.length;
    c.user = this.actualItemTurn.assignedTurn;
    chats.push(c);
    let cs = JSON.stringify(chats);
    this.updateItemChat(this.actualItemTurn.chatId,'chats', cs);
    this.someEvent(this.actualItemTurn,'I', msg);
  }

  itemEvent(items:anItem[]) {
    debugger;
    let cs = JSON.stringify(items);
    this.updateItemChat(this.actualItemTurn.itemId,'items', cs);
    this.someEvent(this.actualItemTurn,'Q', 'Items...');
  }

  Securityinfo(secLevel:number) {
    let msg: string='';
    if(secLevel>0) {msg='<strong>Nombre: </strong>'+this.itemClient.title;}
    if(secLevel>1) {msg='<strong>Identificación: </strong>'+this.itemClient.id+'<br>'+msg;}
    if(secLevel>2) {
      msg +='<strong>Teléfono/Celular: </strong>'+this.itemClient.phone+'<br>'+
      '<strong>Correo: </strong>'+this.itemClient.email+'<br>'+
      '<strong>Dirección: </strong>'+this.itemClient.addr+'<br>'
    }
    return msg;
  }

}



