import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';

import { AngularFireModule } from '@angular/fire';
import { firebaseConfig  } from '../environment';
import { AngularFirestoreModule } from '@angular/fire/firestore';
import { FirebaseServiceProvider } from '../providers/firebase-service/firebase-service';

import { IonicStorageModule } from '@ionic/storage';
import { MainMenuComponent } from '../components/main-menu/main-menu';
import { ConfigPage } from '../pages/config/config';
import { InfoPage } from '../pages/info/info';
import { QscannerComponent } from '../components/qscanner/qscanner';
import { DetailPage } from '../pages/detail/detail';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { DetaChatComponent } from '../components/deta-chat/deta-chat';
import { DetaItemComponent } from '../components/deta-item/deta-item';

import { ZXingScannerModule } from '@zxing/ngx-scanner';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    MainMenuComponent,
    QscannerComponent,
    InfoPage,
    ConfigPage,
    DetailPage,
    DetaChatComponent,
    DetaItemComponent
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFirestoreModule.enablePersistence(),
    IonicStorageModule.forRoot(),
    ZXingScannerModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    MainMenuComponent,
    QscannerComponent,
    InfoPage,
    ConfigPage,
    DetailPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    FirebaseServiceProvider,
    BarcodeScanner
  ]
})
export class AppModule {}



