import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import { turn } from '../../tiposdatos';
import { FirebaseServiceProvider } from '../../providers/firebase-service/firebase-service';

/**
 * Generated class for the DetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-detail',
  templateUrl: 'detail.html',
})
export class DetailPage implements OnInit {

  itemTurn: turn = {};
  actualComponent: string = '0';


  constructor(public navCtrl:NavController, private fds:FirebaseServiceProvider) {
  }

  ngOnInit(){
    //called after the constructor and called  after the first ngOnChanges() 
    this.itemTurn = this.fds.actualItemTurn;
    if(this.itemTurn.event ==='I') {
      this.actualComponent = '2' //Chats
    } else {
      this.actualComponent = '1' //Items
    }
  }

  showDetaComponent(i:string) {
    this.actualComponent=i;
  }

}



